, style);\n      $color: map-get(map-get($borders, $direction), color);\n      border-left: $size $style $color;\n    }\n  }\n}\n",".avatar {\n  @include avatar($avatar-width,$avatar-status-width);\n\n  > img {\n    @extend .img-avatar;\n    vertical-align: initial;\n  }\n}\n\n.avatar-lg {\n  @include avatar($avatar-lg-width,$avatar-lg-status-width);\n}\n\n.avatar-sm {\n  @include avatar($avatar-sm-width,$avatar-sm-status-width);\n}\n\n.avatar-xs {\n  @include avatar($avatar-xs-width,$avatar-xs-status-width);\n}\n\n.avatars-stack {\n\n  .avatar {\n    margin-right: - ($avatar-width / 2);\n    transition: margin-right $layout-transition-speed;\n\n    &:hover {\n      margin-right: 0;\n    }\n  }\n\n  .avatar-lg {\n    margin-right: - ($avatar-lg-width / 2);\n  }\n\n  .avatar-sm {\n    margin-right: - ($avatar-sm-width / 2);\n  }\n\n  .avatar-xs {\n    margin-right: - ($avatar-xs-width / 2);\n  }\n}\n","@mixin avatar($width, $status-width) {\n  position: relative;\n  display: inline-block;\n  width: $width;\n  height: $width;\n\n  .avatar-status {\n    position: absolute;\n    right: 0;\n    bottom: 0;\n    display: block;\n    width: $status-width;\n    height: $status-width;\n    border: 1px solid #fff;\n    border-radius: 50em;\n  }\n}\n",".badge-pill {\n  border-radius: $badge-pill-border-radius;\n}\n","\n\n\n@media screen and (min-width: 701px) and (max-width: 2560px) {\n\n    .user-area{ width:200px; margin:0 0 0 30px; text-align: left;\n        .profile-pic{ width: 40px; height:40px; float: left;}\n        .user-name{ width:125px; height:30px; padding:4px 0 0 4px; float: left; color: #000;}\n        p{ font-size: 11px;}\n    }\n    \n    .user-area-dropdown ul{\n        padding:10px!important;\n    }\n    .user-area-dropdown ul li {padding: 10px; list-style: none; text-align: left;}\n    \n\n    .dark-mode{\n        .user-area{ width:200px; margin:0 0 0 30px; text-align: left;\n            .profile-pic{ width: 40px; height:40px; float: left;}\n            .user