n    margin-left: initial;\n  }\n}\n",".breadcrumb {\n  position: fixed;\n  @include border-radius($breadcrumb-border-radius);\n  // @include borders($breadcrumb-borders);\n}\n\n*[dir=\"rtl\"] {\n  .breadcrumb-item::before {\n    padding-right: 0;\n    padding-left: $breadcrumb-item-padding;\n  }\n  .breadcrumb-item {\n    padding-right: $breadcrumb-item-padding;\n    padding-left: 0;\n  }\n}\n","//\n// Base styles\n//\n\n.brand-card {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  min-width: 0;\n  margin-bottom: ($spacer * 1.5);\n  word-wrap: break-word;\n  background-color: $card-bg;\n  background-clip: border-box;\n  border: $card-border-width solid $card-border-color;\n  @include border-radius($card-border-radius);\n}\n\n.brand-card-header {\n  position: relative;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  height: 6rem;\n  @include border-radius($card-border-radius $card-border-radius 0 0);\n\n  i {\n    font-size: 2rem;\n    color: #fff;\n  }\n\n  .chart-wrapper {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n  }\n}\n\n.brand-card-body {\n  display: flex;\n  flex-direction: row;\n  padding: $card-spacer-y 0;\n  text-align: center;\n\n  > * {\n    flex: 1;\n    padding: ($card-spacer-y * .25) 0;\n  }\n\n  > *:not(:last-child) {\n    border-right: 1px solid $border-color;\n  }\n}\n\n// stylelint-disable selector-max-universal\n// Right-to-Left Support\n*[dir=\"rtl\"] {\n  .brand-card-body {\n    > *:not(:last-child) {\n      border-right: 0;\n      border-left: 1px solid $border-color;\n    }\n  }\n}\n","//\n// Base styles\n//\n\n.btn-brand {\n  border: 0;\n  i {\n    display: inline-block;\n    width: (($btn-padding-y * 2) + ($font-size-base * $btn-line-height));\n    margin: (- $btn-padding-y) (- $btn-padding-x);\n    line-height: (($btn-padding-y * 2) + ($font-size-base * $btn-line-height));\n    text-align: center;\n    background-color: rgba(0, 0, 0, .2);\n    @include border-radius($btn-border-radius);\n  }\n\n  i + span {\n    margin-left: ($btn-padding-x * 2);\n  }\n\n  &.btn-lg {\n    i {\n      width: (($btn-padding-y-lg * 2) + ($font-size-lg * $btn-line-height-lg));\n      margin: (- $btn-padding-y-lg) (- $btn-padding-x-lg);\n      line-height: (($btn-padding-y-lg * 2) + ($font-size-lg * $btn-line-height-lg));\n      @include border-radius($btn-border-radius-lg);\n    }\n\n    i + span {\n      margin-left: ($btn-padding-x-lg * 2);\n    }\n  }\n\n  &.btn-sm {\n    i {\n      width: (($btn-padding-y-sm * 2) + ($font-size-sm * $btn-line-height-sm));\n      margin: (- $btn-padding-y-sm) (- $btn-padding-x-sm);\n      line-height: (($btn-padding-y-sm * 2) + ($font-size-sm * $btn-line-height-sm));\n      @include border-radius($btn-border-radius-sm);\n    }\n\n    i + span {\n      margin-left: ($btn-padding-x-sm * 2);\n    }\n  }\n\n  &.btn-square i {\n    border-radius: 0;\n  }\n}\n\n//\n// Variants\n//\n\n@each $color, $value in $brands-colors {\n  .btn-#{$color} {\n    @include button-variant($value, $value);\n  }\n}\n","// stylelint-disable selector-no-qualifying-type\n\n*[dir=\"rtl\"] {\n  .btn-group {\n    // Prevent double borders when buttons are next to each other\n    > .btn:not(:first-child),\n    > .btn-group:not(:first-child) {\n      margin-right: -$btn-border-width;\n    }\n\n    // Reset rounded corners\n    > .btn:not(:last-child):not(.dropdown-toggle),\n    > .btn-group:not(:last-child) > .btn {\n      @include border-radius($border-radius, 0);\n      @include border-left-radius(0);\n    }\n\n    > .btn:not(:first-child),\n    > .btn-group:not(:first-child) > .btn {\n      @include border-radius($border-radius, 0);\n      @include border-right-radius(0);\n    }\n\n    > .btn:not(:first-child):not(:last-child):not(.dropdown-toggle) {\n      @include border-left-radius(0);\n      @include border-right-radius(0);\n    }\n  }\n}\n","//button {\n//  @extend .btn;\n//}\n\n.btn-transparent {\n  color: #fff;\n  