 $active-border;\n  }\n\n  &:focus,\n  &.focus {\n    box-shadow: 0 0 0 $btn-focus-width rgba($color, .5);\n  }\n\n  &.disabled,\n  &:disabled {\n    color: $color;\n    background-color: transparent;\n    border-color: transparent;\n  }\n\n  &:not(:disabled):not(.disabled):active,\n  &:not(:disabled):not(.disabled).active,\n  .show > &.dropdown-toggle {\n    color: color-yiq($active-background);\n    background-color: $active-background;\n    border-color: $active-border;\n\n    &:focus {\n      // Avoid using mixin so we can pass custom focus shadow properly\n      @if $enable-shadows and $btn-active-box-shadow != none {\n        box-shadow: $btn-active-box-shadow, 0 0 0 $btn-focus-width rgba($color, .5);\n      } @else {\n        box-shadow: 0 0 0 $btn-focus-width rgba($color, .5);\n      }\n    }\n  }\n}\n","//\n// Base styles\n//\n\n.callout {\n  position: relative;\n  padding: 0 $spacer;\n  margin: $spacer 0;\n  border-left: 4px solid $border-color;\n\n  @if $enable-rounded {\n    border-radius: $border-radius;\n  }\n\n  .chart-wrapper {\n    position: absolute;\n    top: 10px;\n    left: 50%;\n    float: right;\n    width: 50%;\n  }\n}\n\n.callout-bordered {\n  border: 1px solid $border-color;\n  border-left-width: 4px;\n}\n.callout code {\n  border-radius: $border-radius;\n}\n.callout h4 {\n  margin-top: 0;\n  margin-bottom: .25rem;\n}\n.callout p:last-child {\n  margin-bottom: 0;\n}\n.callout + .callout {\n  margin-top: - .25rem;\n}\n\n//\n// Variants\n//\n\n@each $color, $value in $theme-colors {\n  .callout-#{$color} {\n    border-left-color: $value;\n\n    h4 {\n      color: $value;\n    }\n  }\n}\n\n// Right-to-Left Support\n*[dir=\"rtl\"] {\n  .callout {\n    border-right: 4px solid $border-color;\n    border-left: 0;\n\n    @each $color, $value in $theme-colors {\n      &.callout-#{$color} {\n        border-right-color: $value;\n      }\n    }\n\n    .chart-wrapper {\n      left: 0;\n      float: left;\n    }\n  }\n}\n",".card {\n  margin-bottom: ($spacer * 1.5);\n  padding:15px;\n\n  // Cards with color accent\n  @each $color, $value in $theme-colors {\n    &.bg-#{$color} {\n      border-color: darken($value, 12.5%);\n      .card-header {\n        background-color: darken($value, 3%);\n        border-color: darken($value, 12.5%);\n      }\n    }\n  }\n\n  &.drag,\n  .drag {\n    cursor: move;\n  }\n}\n\n.card-placeholder {\n  background: rgba(0, 0, 0, .025);\n  border: 1px dashed $gray-300;\n}\n\n.card-header {\n  > i {\n    margin-right: $spacer / 2;\n  }\n\n  .nav-tabs {\n    margin-top: -$card-spacer-y;\n    margin-bottom: -$card-spacer-y;\n    border-bottom: 0;\n\n    .nav-item {\n      border-top: 0;\n    }\n\n    .nav-link {\n      padding: $card-spacer-y ($card-spacer-x / 2);\n      color: $text-muted;\n      border-top: 0;\n\n      &.active {\n        color: $body-color;\n        background: #fff;\n      }\n    }\n  }\n}\n\n*[dir=\"rtl\"] .card-header > i {\n  margin-right: 0;\n  margin-left: $spacer / 2;\n}\n\n.card-header-icon-bg {\n  display: inline-block;\n  width: ($card-spacer-y * 2) + ($font-size-base * $line-height-base);\n  padding: $card-spacer-y 0;\n  margin: (- $card-spacer-y) $card-spacer-x (- $card-spacer-y) (- $card-spacer-x);\n  line-height: inherit;\n  color: $card-icon-color;\n  text-align: center;\n  background: $card-icon-bg;\n  border-right: $card-border-width solid $card-border-color;\n}\n\n.card-header-actions {\n  display: inline-block;\n  float: right;\n  margin-right: - ($spacer / 4);\n}\n\n*[dir=\"rtl\"] {\n  .card-header-actions {\n    float: left;\n    margin-right: auto;\n    margin-left: - ($spacer / 4);\n  }\n}\n\n.card-header-action {\n  padding: 0 ($spacer / 4);\n  color: $gray-600;\n\n  &:hover {\n    color: $body-color;\n    text-decoration: none;\n  }\n}\n\n\n// Cards with color accent\n@each $color, $value in $theme-colors {\n  .card-accent-#{$color} {\n    @include card-accent-variant($value);\n  }\n}\n\n.card-full {\n  margin-top: - $spacer;\n  margin-right: - $gr