        border-left: 0;\n\n        .nav-icon {\n          display: block;\n          float: left;\n          width: $sidebar-minimized-height;\n          font-size: 18px;\n          margin-left:0px;\n          border-left:0px;\n        }\n\n        .badge {\n          position: absolute;\n          right: 15px;\n          display: none;\n        }\n        \n\n        &:hover {\n          width: $sidebar-width + $sidebar-minimized-width;\n          background: $sidebar-nav-link-hover-bg;\n\n          .badge {\n            display: inline;\n          }\n        }\n\n        &.nav-dropdown-toggle::before {\n          display: none;\n        }\n      }\n\n      .nav-dropdown-items {\n        .nav-item {\n          width: $sidebar-width;\n\n          .nav-link {\n            width: $sidebar-width;\n          }\n        }\n      }\n\n      .nav > .nav-dropdown {\n        > .nav-dropdown-items {\n          display: none;\n          max-height: 1000px;\n          // background: #f6f7ff;\n          margin-left:0px;\n        }\n\n        &:hover {\n          background: $sidebar-nav-link-hover-bg;\n          > .nav-dropdown-items {\n            position: absolute;\n            left: $sidebar-minimized-width;\n            display: inline;\n          }\n        }\n      }\n    }\n  }\n\n  *[dir=\"rtl\"] {\n    .sidebar-minimized {\n\n\n      \n      .sidebar {\n        .nav {\n          list-style-image: url(\"data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7\");\n          .divider {\n            height: 0;\n          }\n        }\n\n\n        \n    \n\n        //.d-minimized-none,\n        //.nav-divider,\n        //.nav-label,\n        //.nav-title,\n        //.sidebar-footer,\n        //.sidebar-form,\n        //.sidebar-header {\n        //  display: none;\n        //}\n\n        .sidebar-minimizer::before {\n          width: 100%;\n          transform: rotate(0deg);\n        }\n\n        .nav-link {\n          padding-right: 0;\n\n          .nav-icon {\n            float: right;\n          }\n\n          .badge {\n            right: auto;\n            left: 15px;\n          }\n\n          &:hover {\n            .badge {\n              display: inline;\n            }\n          }\n        }\n\n        .nav > .nav-dropdown {\n          > .nav-dropdown-items {\n            display: none;\n            max-height: 1000px;\n            background: $sidebar-bg;\n          }\n\n          &:hover {\n            background: $sidebar-nav-link-hover-bg;\n            > .nav-dropdown-items {\n              position: absolute;\n              left: 0;\n              display: inline;\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n*[dir=\"rtl\"] {\n  .sidebar {\n    .nav-dropdown-toggle::before {\n      position: absolute;\n      right: auto;\n      left: $sidebar-nav-link-padding-x;\n      transform: rotate(180deg);\n    }\n\n    .nav-dropdown.open {\n      > .nav-dropdown-toggle::before {\n        transform: rotate(270deg);\n      }\n    }\n\n    .nav-link {\n      .nav-icon {\n        margin: 0 0 0 ($sidebar-nav-link-padding-x / 2);\n      }\n\n      .badge {\n        float: left;\n        margin-top: 2px;\n      }\n    }\n\n    .nav-link.nav-dropdown-toggle {\n      .badge  {\n        margin-right: auto;\n        margin-left: 16px;\n      }\n    }\n\n    .sidebar-minimizer::before {\n      right: auto;\n      left: 0;\n      transform: rotate(180deg);\n    }\n  }\n\n  // Temp fix for rtl sidebar-toggler\n  // ToDo: find better solution\n  .sidebar-toggler {\n    margin-right: 0 !important;\n  }\n}\n\n\n\n// ------------------------darkmode\n\n\n\n// .dark-mode {\n// .sidebar-minimized .sidebar .nav-dropdown-items .nav-item .nav-link {\n//     background: #FCC;\n// }\n// }\n\n\n\n\n// nav {    \n//     display: block;\n//     text-align: center;\n//   }\n//   nav ul {\n//     margin: 0;\n//     padding:0;\n//     list-style: none;\n//   }\n//   .nav a {\n//     display:block; \n//     background: #111; \n//     color:#fff; \n//     text-decoration: none;\n//     padding: .8em 1.8em;\n//     text-transform: uppercase;\n//     font-size: 80%;letter-spacing: 2px;\n//     text-shadow: 0 -1px 0 #000;\n//     position: relative;\n//   }\n//   .nav{  \n//     vertical-align: top; \n//     display: inline-block;\n//     box-shadow: 1px -1px -1px 1px #000, -1px 1px -1px 1px #fff, 0 0 6px 3px #fff;\n//     border-radius:6px;\n//   }\n//   .nav li{position: relative;}\n//   .nav > li { \n//     float:left; \n//     border-bottom: 4px #aaa solid; \n//     margin-right: 1px; \n//   } \n//   .nav > li > a { \n//     margin-bottom:1px;\n//     box-shadow:inset 0 2em .33em -.5em #555; \n//   }\n//   .nav > li:hover , .nav > li:hover >a{  border-bottom-color:orange;}\n//   .nav li:hover > a { color:orange; }\n//   .nav > li:first-child  { border-radius: 4px 0 0 4px;} \n//   .nav > li:first-child>a{border-radius: 4px 0 0 0;}\n//   .nav > li:last-child  { \n//     border-radius: 0 0 4px 0; \n//     margin-right: 0;\n//   } \n//   .nav > li:last-child >a{border-radius: 0 4px 0 0; }\n//   .nav li li a { margin-top:1px}\n  \n  \n  \n//     .nav li a:first-child:nth-last-child(2):before { \n//      content:\"\"; \n//      position: absolute; \n//      height:0; \n//      width: 0; \n//      border: 5px solid transparent; \n//      top: 50% ;\n//      right:5px;  \n//    }\n   \n   \n   \n   \n   \n//    /* submenu positioning*/\n// .nav ul {\n//   position: absolute;\n//   white-space: nowrap;\n//   border-bottom: 5px solid  orange;\n//   z-index: 1;\n//   left: -99999em;\n// }\n// .nav > li:hover > ul {\n//   left: auto;\n//   padding-top: 5px  ;\n//   min-width: 100%;\n// }\n// .nav > li li ul {  border-left:1px solid #fff;}\n\n\n// .nav > li li:hover > ul { \n//  /* margin-left: 1px */\n//   left: 100%;\n//   top: -1px;\n// }\n// /* arrow hover styling */\n// .nav > li > a:first-child:nth-last-child(2):before { \n//   border-top-color: #aaa; \n// }\n// .nav > li:hover > a:first-child:nth-last-child(2):before {\n//   border: 5px solid transparent; \n//   border-bottom-color: orange; \n//   margin-top:-5px\n// }\n// .nav li li > a:first-child:nth-last-child(2):before {  \n//   border-left-color: #aaa; \n//   margin-top: -5px\n// }\n// .nav li li:hover > a:first-child:nth-last-child(2):before {\n//   border: 5px solid transparent; \n//   border-right-color: orange;\n//   right: 10px; \n// }\n\n\n\n.sidenavbar-main{\n  .nav-item{list-style: none;}\n  ul{list-style: none;}\n  ul li {list-style: none;}\n  \n}\n\n\n.version-alpha{\n  width:40px; border-radius:10px; \n  background: #503eff;\n color: #FFF;\n font-weight: bold;position:absolute; z-index:10000; top:10px; right:15px; font-size:10px; padding:0px; text-align:center;\n}\n\n\n.sidebar-minimizer {\n\n  .sidenavbar-main{\n    overflow: hidden;\n    .nav-item{list-style: none;}\n    ul{list-style: none;}\n    ul li {list-style: none;} \n\n  }\n  \n  .version-alpha{\n    width:40px; border-radius:10px; background:#FCC;position:absolute; z-index:10000; top:10px; right:5px; font-size:10px; padding:0px; text-align:center;\n  }\n\n}\n\n\n\n\n\n\n// .home-icon:hover{\n//   // width:25px;\n//   // height:25px;\n//   background:url('assets/icons/svg/bell.svg') no-repeat;\n// }\n\n\n","@mixin sidebar-width($borders, $width) {\n  $sidebar-width: $width;\n\n  @each $border in $borders {\n    $direction: nth($border, 1);\n    @if $direction == \"all\" {\n      $size: map-get(map-get($borders, $direction), size);\n      $sidebar-width: ($sidebar-width - (2 * $size));\n    } @else if $direction == \"right\" {\n      $size: map-get(map-get($borders, $direction), size);\n      $sidebar-width: $sidebar-width - $size;\n    } @else if $direction == \"left\" {\n      $size: map-get(map-get($borders, $direction), size);\n      $sidebar-width: $sidebar-width - $size;\n    }\n    width: $sidebar-width;\n  }\n}\n","//\n// Base styles\n//\n\n.switch {\n  display: inline-block;\n  width: $switch-width;\n  height: $switch-height;\n}\n\n.switch-input {\n  display: none;\n}\n\n.switch-slider {\n  position: relative;\n  display: block;\n  height: inherit;\n  cursor: pointer;\n  background-color: #fff;\n  border: $border-width solid $border-color;\n  transition: .15s ease-out;\n  @include border-radius($border-radius);\n\n  &::before {\n    position: absolute;\n    top: $switch-handle-margin;\n    left: $switch-handle-margin;\n    box-sizing: border-box;\n    width: $switch-height - ($switch-handle-margin * 2) - ($border-width * 2);\n    height: $switch-height - ($switch-handle-margin * 2) - ($border-width * 2);\n    content: \"\";\n    background-color: #fff;\n    border: 1px solid $border-color;\n    transition: .15s ease-out;\n    @include border-radius($border-radius / 2);\n  }\n}\n\n.switch-input:checked ~ .switch-slider {\n  &::before {\n    transform: translateX($switch-width - $switch-height);\n  }\n}\n\n.switch-input:disabled ~ .switch-slider {\n  cursor: not-allowed;\n  opacity: .5;\n}\n\n//\n// Switch Sizes\n//\n\n.switch-lg {\n  @include switch-size($switch-lg-width, $switch-lg-height, $switch-lg-font-size, $switch-handle-margin);\n}\n\n.switch-sm {\n  @include switch-size($switch-sm-width, $switch-sm-height, $switch-sm-font-size, $switch-handle-margin);\n}\n\n//\n// Switch with label\n//\n.switch-label {\n  width: $switch-label-width;\n\n  .switch-slider {\n    &::before {\n      z-index: 2;\n    }\n    &::after {\n      position: absolute;\n      top: 50%;\n      right: 1px;\n      z-index: 1;\n      width: 50%;\n      margin-top: -.5em;\n      font-size: $switch-font-size;\n      font-weight: 600;\n      line-height: 1;\n      color: $gray-300;\n      text-align: center;\n      text-transform: uppercase;\n      content: attr(data-unchecked);\n      transition: inherit;\n    }\n  }\n\n  .switch-input:checked ~ .switch-slider {\n    &::before {\n      transform: translateX($switch-label-width - $switch-height);\n    }\n    &::after {\n      left: 1px;\n      color: #fff;\n      content: attr(data-checked);\n    }\n  }\n\n  // Sizes\n  &.switch-lg {\n    @include switch-size($switch-label-lg-width, $switch-lg-height, $switch-lg-font-size, $switch-handle-margin);\n  }\n\n  &.switch-sm {\n    @include switch-size($switch-label-sm-width, $switch-sm-height, $switch-sm-font-size, $switch-handle-margin);\n  }\n}\n\n//\n// Switch 3d\n//\n\n.switch-3d {\n  .switch-slider {\n    background-color: $gray-100;\n    border-radius: 50em;\n\n    &::before {\n      top: -1px;\n      left: -1px;\n      width: $switch-height;\n      height: $switch-height;\n      border: 0;\n      border-radius: 50em;\n      box-shadow: 0 2px 5px rgba(0, 0, 0, .3);\n    }\n  }\n\n  // Sizes\n  &.switch-lg {\n    @include switch-3d-size($switch-lg-width, $switch-lg-height);\n  }\n\n  &.switch-sm {\n    @include switch-3d-size($switch-sm-width, $switch-sm-height);\n  }\n}\n\n//\n// Switch Variants\n//\n\n@each $color, $value in $theme-colors {\n  //normal style\n  .switch-#{$color} {\n    @include switch-variant($value);\n  }\n  //outline style\n  .switch-outline-#{$color} {\n    @include switch-outline-variant($value);\n  }\n  //outline alternative style\n  .switch-outline-#{$color}-alt {\n    @include switch-outline-variant-alt($value);\n  }\n}\n\n//\n// Pill Style\n//\n\n.switch-pill {\n  .switch-slider {\n    border-radius: 50em;\n\n    &::before {\n      border-radius: 50em;\n    }\n  }\n}\n","@mixin switch-size($width, $height, $font-size, $handle-margin) {\n  width: $width;\n  height: $height;\n\n  .switch-slider {\n    font-size: $font-size;\n\n    &::before {\n      width: $height - ($handle-margin * 2) - ($border-width * 2);\n      height: $height - ($handle-margin * 2) - ($border-width * 2);\n    }\n    &::after {\n      font-size: $font-size;\n    }\n  }\n\n  .switch-input:checked ~ .switch-slider {\n    &::before {\n      transform: translateX($width - $height);\n    }\n  }\n}\n\n@mixin switch-3d-size($width, $height) {\n  width: $width;\n  height: $height;\n\n  .switch-slider::before {\n    width: $height;\n    height: $height;\n  }\n\n  .switch-input:checked ~ .switch-slider {\n    &::before {\n      transform: translateX($width - $height);\n    }\n  }\n}\n\n@mixin switch-variant($color) {\n  .switch-input:checked + .switch-slider {\n    background-color: $color;\n    border-color: darken($color, 10%);\n\n    &::before {\n      border-color: darken($color, 10%);\n    }\n  }\n}\n\n@mixin switch-outline-variant($color) {\n  .switch-input:checked + .switch-slider {\n    background-color: #fff;\n    border-color: $color;\n\n    &::before {\n      border-color: $color;\n    }\n    &::after {\n      color: $color;\n    }\n  }\n}\n\n@mixin switch-outline-variant-alt($color) {\n  .switch-input:checked + .switch-slider {\n    background-color: #fff;\n    border-color: $color;\n\n    &::before {\n      background-color: $color;\n      border-color: $color;\n    }\n    &::after {\n      color: $color;\n    }\n  }\n}\n",".table-outline {\n  border: 1px solid $table-border-color;\n\n  td {\n    vertical-align: middle;\n  }\n}\n\n.table-align-middle {\n\n  td {\n    vertical-align: middle;\n  }\n}\n\n.table-clear {\n  td {\n    border: 0;\n  }\n}\n","\n\n.tickets-list{\n    width:100%;\n    background: #F7F7F7;\n    margin: 2px 0;\n    .ticket-item{ \n        height:70px;\n        width:100%;\n        padding:5px;\n        .user-letter{\n            width: 20%;\n            height: 100%;\n            float: left;\n            padding: 4%;\n            .letter-icon{\n                font-size: 20px;\n    font-weight: bold;\n    color: #000;\n    background: #FDCC;\n    border-radius: 23px;\n    width: 48px;\n    height: 48px;\n    padding: 10px 17px;\n    margin: 2% auto;\n            }\n        }\n        .user-details{\n            width:60%;\n            float:left;\n            line-height: 20px;\n        }\n        .time{\n            width:20%;\n            float:left;\n            text-align:bottom;\n        }\n    }\n\n}\