\n.tab {\n  float: left;\n  border: 1px solid #ccc;\n  background-color: #f1f1f1;\n  width: 30%;\n}\n\n/* Style the buttons inside the tab */\n.tab button {\n  display: block;\n  background-color: inherit;\n  color: black;\n  padding: 22px 16px;\n  width: 100%;\n  border: none;\n  outline: none;\n  text-align: left;\n  cursor: pointer;\n  transition: 0.3s;\n  font-size: 17px;\n}\n\n/* Change background color of buttons on hover */\n.tab button:hover {\n  background-color: #ddd;\n  color:#525252;\n}\n\n\n\n/* Create an active/current \"tab button\" class */\n.tab button.active {\n  background-color: #ccc;\n}\n\n/* Style the tab content */\n.tabcontent {\n  float: left;\n  padding: 0px 12px;\n  border: 1px solid #ccc;\n  width: 70%;\n  border-left: none;\n  height: 300px;\n}\n\n\n\n.inner-vertical-tabs{\n\n  .internal-nav-ttile{\n    width:85%;\n    float:left;\n    h5{\n      \n      font-size: 14px;\n    color:#1d2a52;\n    margin-bottom: 0.2rem;\n    }\n    p{\n      margin:0px;\n    }\n  }\n    .tab {\n        float: left;\n        border: 0px solid #ccc;\n        background-color: #F9F9F9;\n        width: 100%;\n        height:auto;\n        padding:4px 10px;\n        margin-top:0px;\n      }\n      .tab:hover{ background-color: rgb(227, 233, 243);}\n\n      \n\n      .active{\n        float: left;\n        border-left: 5px solid #1D1C8B;\n        background-color: #FFF;\n        width: 100%;\n        height:auto;\n        h5{\n          color:$primary;\n        }\n        margin-top:0px;        \n        // padding:4px 10px;\n      }\n      a {margin-top:1px;}\n\n      \n\n}\n\n\n.dark-mode{\n\n.inner-vertical-tabs{\n\n  .internal-nav-ttile{\n    width:85%;\n    float:left;\n    h5{\n      \n      font-size: 14px;\n    color:#FFF;\n    margin-bottom: 0.2rem;\n    }\n    p{\n      margin:0px;\n      color:#d8d8d8;\n    }\n  }\n    .tab {\n        float: left;\n        border: 0px solid #ccc;\n        background-color: #505050;\n        width: 100%;\n        height:auto;\n        padding:4px 10px;\n        margin-top:0px;\n      }\n      .tab:hover{ background-color: rgb(108, 152, 223);\n      p{color:#d8d8d8;}\n    }\n\n      \n\n      .active{\n        float: left;\n        border-left: 5px solid #4263C7;\n        background-color: #FFF;\n        width: 100%;\n        height:auto;\n        h5{\n          color:#FFF;\n        }\n        margin-top:0px;        \n        // padding:4px 10px;\n      }\n      a {margin-top:1px;}\n\n      \n\n}\n\n}","\n\n\n// select{\n// \theight:40px; \n// \tbackground:#FCC;\n// \tborder: 1px solid rgb(212, 146, 146);\n// \tfont-size: 14px;\n// \toption{\n// \t\theight:35px;\n\n// \t}\n// }\n\n\n\n\n\n\n\n.select {\n  \n\tposition: relative;\n    border-radius: 5px;\n    width: 100%;\n\t// height: 38px;\n}\n\n.select-text {\n\tposition: relative;\n    // background-color: transparent;\n\tbackground-color: #FFF;\n\tborder-radius:5px;\n\twidth:100%;\n\tfont-weight:400;\n    \n    padding: 1.5rem 0.5rem 0.5rem 10px;\n\tfont-size: 15px;\n}\n\n/* Remove focus */\n.select-text:focus {\n    outline: none;\n    border-color:$primary;  \n\t// border-bottom: 1px solid rgba(0,0,0, 0);\n}\n\n\t/* Use custom arrow */\n.select .select-text {\n\tappearance: none;\n\t-webkit-appearance:none\n}\n\n.select:after {\n\tposition: absolute;\n\ttop: 61%;\n    right: 20px;\n\t/* Styling the down arrow */\n\twidth: 0;\n\theight: 0;\n\tpadding: 0;\n\tcontent: '';\n\tborder-left: 6px solid transparent;\n\tborder-right: 6px solid transparent;\n\tborder-top: 6px solid rgba(0, 0, 0, 0.12);\n\tpointer-events: none;\n}\n\n\n/* LABEL ======================================= */\n.select-label {\n\t// color: rgba(0,0,0, 0.26);\n\n\n\n\tfont-size: 1rem;\n\tfont-weight: normal;\n\tposition: absolute;\n\tpointer-events: none;\n\tleft: 0;\n\t\n    transition: .1s ease-out;\n}\n\n/* active state */\n.select-text:focus ~ .select-label, .select-text:valid ~ .select-label {\n\t// color: #2F80ED;\n\ttop: 28%;\n\tpadding:0px 5px;\n\ttransition: 0.2s ease all;\n    font-size: 0.8rem;\n    border-color:$primary;  \n    color: $primary;\n    transform: translateY(-50%) scale(.9);\n    width:auto;\n}\n\n/* BOTTOM BARS ================================= */\n.select-bar {\n\tposition: relative;\n\tdisplay: block;\n\twidth: 350px;\n}\n\n.select-bar:before, .select-bar:after {\n\tcontent: '';\n\theight: 2px;\n\twidth: 0;\n\t// bottom: 1px;\n\tposition: absolute;\n\t// background: $primary;\n\t// transition: 0.2s ease all;\n}\n\n.select-bar:before {\n\tleft: 50%;\n}\n\n.select-bar:after {\n\tright: 50%;\n}\n\n/* active state */\n.select-text:focus ~ .select-bar:before, .select-text:focus ~ .select-bar:after {\n\twidth: 50%;\n}\n\n/* HIGHLIGHTER ================================== */\n.select-highlight {\n\tposition: absolute;\n\tdisplay: none;\n\theight: 60%;\n\twidth: 100px;\n\tbottom: 95%;\n\tleft: 0;\n\tpointer-events: none;\n\topacity: 0.5;\n}\n\n\n.select-wrapper .select-dropdown{\n    position: relative;\n    cursor: pointer;\n    background-color: transparent;\n    \n    border-bottom: 1px solid $gray-500;\n    outline: none;\n    height: 3rem;\n    line-height: 3rem;\n    width: 100%;\n    font-size: 16px;\n    margin: 0 0 8px 0;\n    padding: 0;\n    display: block;\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n    z-index: 1;\n}\n\n.dropdown-content{\n    background-color: #fff;\n    margin: 0;\n    display: none;\n    min-width: 100px;\n    overflow-y: auto;\n    opacity: 0;\n    position: absolute;\n    left: 0;\n    top: 0;\n    z-index: 9999;\n    -webkit-transform-origin: 0 0;\n    transform-origin: 0 0;\n}\n\n.dropdown-content li{\n    clear: both;\n    color: rgba(0,0,0,0.87);\n    cursor: pointer;\n    min-height: 50px;\n    line-height: 1.5rem;\n    width: 100%;\n    text-align: left;\n}\n\n\n\n\n\n","// .container {\n//     display: block;\n//     position: relative;\n//     padding-left: 35px;\n//     margin-bottom: 12px;\n//     cursor: pointer;\n//     // font-size: 22px;\n//     -webkit-user-select: none;\n//     -moz-user-select: none;\n//     -ms-user-select: none;\n//     user-select: none;\n//   }\n  \n  /* Hide the browser's default checkbox */\n  // .container input {\n  //   position: absolute;\n  //   opacity: 0;\n  //   cursor: pointer;\n  //   height: 0;\n  //   width: 0;\n  // }\n  \n  /* Create a custom checkbox */\n  .checkmark {\n    position: absolute;\n    top: 0;\n    left: 0;\n    height: 25px;\n    width: 25px;\n    background-color: #eee;\n  }\n  \n  /* On mouse-over, add a grey background color */\n  .container:hover input ~ .checkmark {\n    background-color: #ccc;\n  }\n  \n  /* When the checkbox is checked, add a blue background */\n  .container input:checked ~ .checkmark {\n    background-color: #2196F3;\n  }\n  \n  /* Create the checkmark/indicator (hidden when not checked) */\n  .checkmark:after {\n    content: \"\";\n    position: absolute;\n    display: none;\n  }\n  \n  /* Show the checkmark when checked */\n  .container input:checked ~ .checkmark:after {\n    display: block;\n  }\n  \n  /* Style the checkmark/indicator */\n  .container .checkmark:after {\n    left: 9px;\n    top: 5px;\n    width: 5px;\n    height: 10px;\n    border: solid white;\n    border-width: 0 3px 3px 0;\n    -webkit-transform: rotate(45deg);\n    -ms-transform: rotate(45deg);\n    transform: rotate(45deg);\n  }","\n\n\n\n.icon{\n   \n    width: 22px; height: 22px; padding:2px; display: inline-block; fill: rgb(53, 53, 53); vertical-align: middle;  background-size: cover; background-position: 0 0;\n    text-align: center; \n}\n\n.icon-32{\n   \n    width: 32px!important ; height: 32px!important; padding:2px; display: inline-block; fill: rgb(53, 53, 53); vertical-align: middle;  background-size: cover; background-position: 0 0;\n    text-align: center; \n}\n\n\n.widget-icon{\n   \n    width: 50px; height: 50px; padding:2px; display: inline-block; fill: rgb(53, 53, 53); vertical-a