-height});\n    }\n  }\n\n  html:not([dir=\"rtl\"]) {\n    .sidebar-compact {\n      .sidebar {\n        margin-left: - $sidebar-compact-width;\n      }\n    }\n    .sidebar-minimized {\n      .sidebar {\n        margin-left: - $sidebar-minimized-width;\n      }\n    }\n  }\n  html[dir=\"rtl\"] {\n    .sidebar-compact {\n      .sidebar {\n        margin-right: - $sidebar-compact-width;\n      }\n    }\n    .sidebar-minimized {\n      .sidebar {\n        margin-right: - $sidebar-minimized-width;\n      }\n    }\n  }\n\n  //\n  // Aside Menu\n  //\n  .aside-menu-fixed {\n    .aside-menu {\n      position: fixed;\n      height: 100%;\n\n      .tab-content {\n        height: calc(100vh - #{$aside-menu-nav-padding-y * 2 + $font-size-base} - #{$navbar-height});\n      }\n    }\n  }\n\n  .aside-menu-fixed {\n    .app-header + .app-body .aside-menu {\n      height: calc(100vh - #{$navbar-height});\n    }\n  }\n\n  .aside-menu-off-canvas {\n    .aside-menu {\n      position: fixed;\n      z-index: $zindex-sticky - 1;\n      height: 100%;\n    }\n  }\n\n  .aside-menu-off-canvas {\n    .app-header + .app-body .aside-menu {\n      height: calc(100vh - #{$navbar-height});\n    }\n  }\n\n  html:not([dir=\"rtl\"]) {\n    .aside-menu-fixed,\n    .aside-menu-off-canvas {\n      .aside-menu {\n        right: 0;\n      }\n    }\n  }\n  html[dir=\"rtl\"] {\n    .aside-menu-fixed,\n    .aside-menu-off-canvas {\n      .aside-menu {\n        left: 0;\n      }\n    }\n  }\n}\n\n//\n// Breadcrumb\n//\n.breadcrumb-fixed {\n  .main {\n    $breadcrumb-height: 2 * $breadcrumb-padding-y + $font-size-base + 1.5 * $spacer;\n    padding-top: $breadcrumb-height;\n  }\n\n  .breadcrumb {\n    position: fixed;\n    top: $navbar-height;\n    right: 0;\n    left: 0;\n    z-index: $zindex-sticky - 3;\n  }\n}\n\n@each $breakpoint in map-keys($grid-breakpoints) {\n  @include media-breakpoint-up($breakpoint) {\n    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);\n    html:not([dir=\"rtl\"]) {\n    